:root {
  --white-text-color: #fff;
  --grey-text-color: #9da4bd;
  --title-black-color: #303030;
  --text-black-color: #555;
  --orange-text-color: #ff6c00;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
  border: 1px solid var(--orange-text-color);
  border-radius: 25px;
}

address {
  font-style: normal;
}

body {
  color: var(--title-black-color);
  font-family: Raleway, sans-serif;
}

body::-webkit-scrollbar {
  display: none;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.container {
  width: 100%;
  margin-left: 50px;
  margin-right: 50px;
  padding-left: 15px;
  padding-right: 15px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.link, .button {
  text-decoration: none;
}

.header-image {
  background-image: url("background-image.bcd4cb04.jpg");
  margin-top: -20px;
}

.header-image::-webkit-scrollbar {
  width: 0;
}

.header-list {
  background-color: rgba(25, 28, 38, .2);
  justify-content: space-around;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 20px;
  font-size: 30px;
  list-style: none;
  display: flex;
}

.header-list::-webkit-scrollbar {
  width: 0;
}

.header-item {
  background-color: #add8e6;
  border-radius: 20px;
  padding: 10px 20px;
}

.header-item a {
  color: #000;
  text-decoration: none;
}

.header-input {
  width: 75%;
  height: 30px;
  margin-top: 30px;
  margin-left: 20px;
}

.phones-list {
  margin-top: 20px;
  margin-bottom: 50px;
  margin-left: 20px;
  list-style: none;
}

.phones-list li {
  margin-top: 20px;
}

.phones-list p {
  margin: 10px auto;
}

.button-item {
  justify-content: center;
  margin-left: 20px;
  padding: 10px;
}

.product-info {
  margin: 10px auto;
}

.modal-card {
  width: 800px;
  background-color: var(--white-text-color);
  border-radius: 30px;
  margin: 20px 0;
  padding: 30px;
}

.modal-card img, h2, h3, p, div {
  margin-top: 10px;
}

.js-list {
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 20px;
  display: flex;
}

.js-card {
  width: 500px;
  height: 550px;
}

.js-card > img:focus, .js-card > img:hover {
  width: 420px;
  height: auto;
  transition-property: width, height;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.phone-sort {
  width: 700px;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 20px auto;
  display: flex;
}

.sort-button {
  justify-content: center;
  padding: 10px 20px;
  font-size: 24px;
}

.basicLightbox {
  width: 100%;
  height: 100vh;
  opacity: .01;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
}

.basicLightbox__placeholder {
  max-width: 100%;
  z-index: 1;
  will-change: transform;
  transition: transform .4s;
  transform: scale(.9);
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  max-width: 95%;
  max-height: 95%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  pointer-events: auto;
}

.basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  width: auto;
  height: auto;
}

.basicLightbox--iframe .basicLightbox__placeholder, .basicLightbox--img .basicLightbox__placeholder, .basicLightbox--video .basicLightbox__placeholder {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

